async function createAndSendJsonFromHighlightsAndTextboxes() {
  const jsonData = await createJsonFromHighlightsAndTextboxes();

  // Define the endpoint URL
  const endpointUrl = "https://localhost:7201/api/DocumentHighlights/UpsertDocumentHighlights";

  // Set up the request options for the fetch call
  const requestOptions = {
      method: 'PUT',
      headers: {
          'Content-Type': 'application/json'
      },
      body: JSON.stringify(jsonData) // Convert the JSON object to a string
  };

  // Use the fetch API to send the JSON data to the server
  fetch(endpointUrl, requestOptions)
      .then(response => {
          if (!response.ok) {
              // Handle HTTP errors
              throw new Error(`HTTP error! Status: ${response.status}`);
          }
          return response.json(); // Parse the JSON response
      })
      .then(data => {
          console.log("Server response:", data);
      })
      .catch(error => {
          console.error("Error sending data to the server:", error);
      });
}

async function createJsonFromHighlightsAndTextboxes() {
  return Word.run(async (context) => {
      // Access the document properties to get the name
      
      // Step 1: Find Highlighted Text Indexes
      const highlightedWordsAndIndexes = await findHighlightedWordsAndIndexes();

      await context.sync();

const docProperties = context.document.properties;
      context.load(docProperties, 'title');

      // Document name
      const documentName = Office.context.document.url || "Untitled Document";

      // Step 2: Retrieve Textbox Values
      const textboxContainer = document.getElementById('textbox-container');
      const textboxes = textboxContainer.getElementsByClassName('text-box');
      let highlights = [];

      for (let textbox of textboxes) {
          if (textbox.value.trim() !== '') {
              highlights.push(textbox.value.trim());
          }
      }

      // Step 3: Construct the JSON Object
      let jsonData = {
          "name": documentName, // Use the document name as the "name" field
          "highlights": highlights,
          "highlightRemovals": highlightedWordsAndIndexes.map(item => {
              return { "start": item.startIndex, "end": item.endIndex };
          })
      };
      console.log(jsonData);
      return jsonData;
  }).catch(error => {
      console.error("Error:", error);
  });
}

async function findHighlightedCharacters() {
  return Word.run(async (context) => {
      // Get all paragraphs in the document
      const paragraphs = context.document.body.paragraphs;
      context.load(paragraphs);
      await context.sync();

      let highlightedWords = [];

      // Iterate through paragraphs
      for (let paragraph of paragraphs.items) {
          // Get the text range for each paragraph
          const textRanges = paragraph.getTextRanges([" "], true);
          context.load(textRanges, 'text, font');
          await context.sync();

          // Iterate through text ranges within the paragraph
          for (let range of textRanges.items) {
              // Check if the range is highlighted
              if (range.font.highlightColor) {
                  // If highlighted, process the range's text
                  const words = range.text.match(/\b[\w']+\b/g); // Simple regex to match words
                  if (words) {
                      highlightedWords = [...highlightedWords, ...words];
                  }
              }
          }
      }

      console.log("Highlighted Words:", highlightedWords);
      return highlightedWords;
  }).catch(error => {
      console.error("Error:", error);
  });
}

async function findHighlightedWordsAndIndexes() {
  return Word.run(async (context) => {
      const paragraphs = context.document.body.paragraphs;
      context.load(paragraphs);
      await context.sync();

      let highlightedWordsAndIndexes = [];

      for (let paragraph of paragraphs.items) {
          const textRanges = paragraph.getTextRanges([" "], true);
          context.load(textRanges, 'text, font');
          await context.sync();

          for (let range of textRanges.items) {
              if (range.font.highlightColor === '#FFFF00') {
                console.log(range);

                let markerIndex = await insertTextMarkerAndCount(context, range);

                // Format the marker to make it less noticeable
                const markerRange = range.insertText('{text-marker}', 'Before');
                markerRange.font.size = 1;
                markerRange.font.color = 'white'; // Assuming a white background
                await context.sync();
                const wordText = range.text;
                let endIndex = markerIndex + wordText.length;
                highlightedWordsAndIndexes.push({ word: wordText, startIndex: markerIndex, endIndex: endIndex });
                const searchResults = context.document.body.search('{text-marker}', { matchCase: true, matchWholeWord: false });
                context.load(searchResults);
                await context.sync();
                searchResults.items[0].delete();
                await context.sync();

                  
              }
          }
      }

      console.log("Highlighted Words and Indexes:", highlightedWordsAndIndexes);
      return highlightedWordsAndIndexes;
  }).catch(error => {
      console.error("Error:", error);
  });
}

async function insertTextMarkerAndCount(context, insertionPoint) {
  // Insert the unique text marker before the specified insertion point
  const range = insertionPoint.insertText('{text-marker}', 'Before');
  
  // Immediately format the inserted marker to make it less noticeable
  range.font.size = 1;  // Set the font size to the smallest possible value
  range.font.color = 'white';  // Set the font color to white, assuming a white background

  await context.sync();

  // Get the whole document's body and its text
  const body = context.document.body;
  context.load(body, 'text');
  await context.sync();

  // Find the position of the unique text marker
  const markerIndex = body.text.indexOf('{text-marker}');

  if (markerIndex === -1) {
      console.error("Text marker not found.");
      return -1; // Marker not found
  }

  // Remove the text marker
  const searchResults = body.search('{text-marker}', { matchCase: true, matchWholeWord: false });
  context.load(searchResults);
  await context.sync();

  searchResults.items[0].delete();
  await context.sync();

  return markerIndex;
}

async function fetchHighlights() {
  return Word.run(async (context) => {
    // Get the document properties to access the document name (title)
    const docProperties = context.document.properties;
    context.load(docProperties, 'title');
    await context.sync();

    // Use the document title as the name parameter in the API call
    const documentName = Office.context.document.url || "Untitled Document";;

    try {
      const response = await fetch(`https://localhost:7201/api/DocumentHighlights/GetDocumentHighlights?name=${documentName}`);
      if (!response.ok) {
        throw new Error(`API call failed: ${response.statusText}`);
      }
      const data = await response.json(); // Parse JSON response
      return data; // Return parsed data
    } catch (error) {
      console.error('Error fetching highlights:', error);
      return null; // Return null or handle error appropriately
    }
  }).catch(error => {
    console.error("Error:", error);
  });
}

// Function to create textboxes from API data
async function createTextboxesFromApi() {
  const data = await fetchHighlights(); // Fetch API data
  
  if (data && data.response && data.response.highlights) {
      const container = document.getElementById('textbox-container');
      data.response.highlights.forEach(highlight => {
          const newTextbox = document.createElement('input');
          newTextbox.type = 'text';
          newTextbox.className = 'text-box';
          newTextbox.value = highlight; // Set textbox value to highlight
          container.appendChild(newTextbox);
          container.appendChild(document.createElement('br'));
      });
  } else {
      console.error('No highlights data found');
  }
  return data;
}

async function reapplyRedHighlightingFromJson(jsonData) {
  return Word.run(async (context) => {
      const body = context.document.body;
      const paragraphs = body.paragraphs;
      context.load(paragraphs, 'text');
      await context.sync();

      let currentCharIndex = 0; // Initialize character count

      // Iterate through paragraphs
      for (let paragraph of paragraphs.items) {
          const textRanges = paragraph.getTextRanges([" "], true);
          context.load(textRanges, 'text');
          await context.sync();

          // Iterate through text ranges within the paragraph
          for (let range of textRanges.items) {
              const rangeText = range.text;
              const rangeLength = rangeText.length + 1; // Including space

              // Check if any highlight removal starts within this range
              for (let removal of jsonData.highlightRemovals) {
                  if (removal.start >= currentCharIndex && removal.start < currentCharIndex + rangeLength) {
                      // Calculate the relative start and end within the range
                      const relativeStart = Math.max(removal.start - currentCharIndex, 0);
                      const relativeEnd = Math.min(removal.end - currentCharIndex, rangeLength);

                      // Highlight the range from relativeStart for the specified length
                      
                      const highlightRange = range.getRange(Word.RangeLocation.start).expandTo(range.getRange(Word.RangeLocation.end));
                      console.log(highlightRange);
                      highlightRange.font.highlightColor = 'yellow'; // Apply red highlight
                      context.load(highlightRange);
                  }
              }

              // Update the current character count
              currentCharIndex += rangeLength; // Include space
          }
      }

      await context.sync();
  }).catch(error => {
      console.error("Error reapplying red highlighting from JSON:", error);
  });
}

async function resetAndFetchData() {
  await removeAllHighlights(); // Remove all highlights from the document
  deleteExistingTextboxes(); // Delete all existing textboxes

  // Now, create new textboxes with API data
  var data = await createTextboxesFromApi();
  await highlightText()

  if (data && data.response){
    await reapplyRedHighlightingFromJson(data.response);
  }
  
}

function removeAllHighlights() {
  return Word.run(async (context) => {
      // Get the entire document body
      var body = context.document.body;

      // Set highlight color to null for the entire body, effectively removing any highlights
      body.font.highlightColor = null;

      await context.sync();
  }).catch((error) => {
      console.error("Error removing highlights:", error);
  });
}

function deleteExistingTextboxes() {
  const container = document.getElementById('textbox-container');
  while (container.firstChild) {
      container.removeChild(container.firstChild);
  }
}

