export async function addTextbox(textValue) {
  // Check if textValue is an event object; if so, set it to an empty string or a default value
  if (textValue instanceof Event) {
      textValue = ''; // or some default value you'd like to use
  }

  console.log('Adding textbox with text: ' + textValue);
  var container = document.getElementById('textbox-container');

  // Create a container for each textbox and its remove button
  var textboxContainer = document.createElement('div');
  textboxContainer.className = 'textbox-wrapper';

  // Create the textbox
  var newTextbox = document.createElement('input');
  newTextbox.type = 'text';
  newTextbox.className = 'text-box';
  newTextbox.value = textValue; // Set the textbox's value to the passed parameter

  // Create the remove button
  var removeButton = document.createElement('button');
  removeButton.textContent = '-';
  removeButton.className = 'remove-textbox';

  // Append the textbox and the remove button to the textbox container
  textboxContainer.appendChild(newTextbox);
  textboxContainer.appendChild(removeButton);

  // Append the textbox container to the main container
  container.appendChild(textboxContainer);

  // Attach event listener to the remove button
  removeButton.addEventListener('click', function() {
      // Remove the textbox container when the remove button is clicked
      removeTextBox(textboxContainer);
      
  });
}

export async function removeTextBox(textboxContainer) {
  console.log('remove');
  var container = document.getElementById('textbox-container');

  // Find the textbox within the container you're about to remove
  var textbox = textboxContainer.querySelector('.text-box');

  // Check if the textbox contains text
  if (textbox && textbox.value.trim() !== '') {
      // Call clearHighlight with the content of the textbox
      await clearHighlight(textbox.value.trim());
  }

  // Remove the textbox container
  container?.removeChild(textboxContainer);
}


export async function highlightText() {
  await Word.run(async (context) => {
      var textboxes = document.getElementsByClassName('text-box');
      var searchQueries = [];

      for (let textbox of textboxes) {
          if (textbox.value) {
              searchQueries.push(textbox.value);
          }
      }

      for (let query of searchQueries) {
          var searchResults = context.document.body.search(query, { matchCase: false, matchWholeWord: true });
          context.load(searchResults, 'items');
          await context.sync();

          for (let result of searchResults.items) {
              result.font.highlightColor = '#FF0000'; // Change as needed
          }
      }

      await context.sync();
  });
}

export async function clearHighlight(textToClear) {
  await Word.run(async (context) => {
    // Search for the text in the document body
    console.log(textToClear);
    var searchResults = context.document.body.search(textToClear, { matchCase: false, matchWholeWord: true });
    
    // Queue a command to load the search results
    context.load(searchResults, 'items');
    await context.sync();

    // Iterate through the search results and clear the highlight
    searchResults.items.forEach((result) => {
      console.log(result);
      result.font.highlightColor = ''; // Attempt to clear the highlight by setting it to 'No Color'
    });

    // Synchronize the document state by executing queued commands
    await context.sync();
  });
}

export async function findBlueHighlights() {
  return Word.run(async (context) => {
      // Get the whole document body range
      const bodyRange = context.document.body.getRange();
      // Split the range by character formats
      var wordRanges = bodyRange.getTextRanges([' '], true);
      context.load(wordRanges, 'items/text, items/font');
      await context.sync();

      let highlightedTexts = [];

      // Iterate over each range
      wordRanges.items.forEach(range => {
        console.log(range.text);
        console.log(range.font.highlightColor);
          if (range.font.highlightColor === "#0000FF") { 
        
            highlightedTexts.push(range.text);
        }
      });

      // Update the HTML list with the highlighted texts
      const list = document.getElementById("highlightedList"); // Ensure this element is in your HTML
      list.innerHTML = ""; // Clear existing list

      highlightedTexts.forEach(text => {
        console.log(text);
          const listItem = document.createElement("li");
          listItem.textContent = text;
          list.appendChild(listItem);
      });
  }).catch(error => {
      console.error("Error: " + error);
      if (error instanceof OfficeExtension.Error) {
          console.log("Debug info: " + JSON.stringify(error.debugInfo));
      }
  });
}